// components/EventCard.js
"use client";
import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardFooter, CardHeader } from '@/components/ui/card';
import { Heart, BookmarkIcon, Info, X, MapPin } from 'lucide-react';

function EventCard({ event, isAdmin, onAction }) {
  const [showRsvpOptions, setShowRsvpOptions] = useState(false);
  const [showDetails, setShowDetails] = useState(false);
  
  if (!event) return null;

  const isLiked = event.isLikedByUser || false;
  const isSaved = event.isSavedByUser || false;
  const likeCount = event.likeCount || 0;
  const saveCount = event.saveCount || 0;
  const participantCount = event.participantCount || 0;

  // Format date for display
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
    });
  };

  return (
    <Card className="overflow-hidden h-full flex flex-col relative">
      <div className="h-40 w-full overflow-hidden bg-gray-200 relative">
        {event.imageUrl ? (
          <img
            src={event.imageUrl}
            alt={event.name}
            className="w-full h-full object-cover"
          />
        ) : (
          <div className="flex items-center justify-center h-full bg-gradient-to-r from-blue-100 to-purple-100">
            <div className="text-2xl font-bold text-gray-500">{event.name.substring(0, 2).toUpperCase()}</div>
          </div>
        )}
        <Badge className="absolute top-2 right-2 bg-opacity-80 backdrop-blur-sm" variant="secondary">
          {event.category || 'Event'}
        </Badge>
        <Button
          variant="secondary"
          size="icon"
          className="absolute bottom-2 right-2 h-8 w-8 rounded-full bg-opacity-70 backdrop-blur-sm"
          onClick={() => setShowDetails(true)}
        >
          <Info className="h-4 w-4" />
        </Button>
      </div>
      <CardHeader className="p-4 pb-2">
        <div className="flex flex-col">
          <div className="text-lg font-bold line-clamp-1">{event.name}</div>
          <div className="text-sm text-muted-foreground">
            {formatDate(event.startDate)}
          </div>
          <div className="text-xs text-muted-foreground mt-1">
            {participantCount} {participantCount === 1 ? 'person' : 'people'}
          </div>
        </div>
      </CardHeader>
      <CardContent className="p-4 pt-0 flex-grow">
        <div className="text-xs text-muted-foreground flex items-center gap-1">
          <MapPin className="h-3 w-3" />
          <span className="line-clamp-1">{event.location}</span>
        </div>
      </CardContent>
      {showDetails && (
        <div className="fixed inset-0 flex items-center justify-center z-50 bg-black/50">
          <div className="bg-white rounded-lg shadow-xl w-full max-w-md max-h-[90vh] overflow-y-auto">
            <div className="flex justify-between items-center p-4 border-b">
              <h3 className="font-bold text-lg">{event.name}</h3>
              <Button variant="ghost" size="icon" onClick={() => setShowDetails(false)}>
                <X className="h-4 w-4" />
              </Button>
            </div>
            <div className="p-4">
              <div className="mb-4">
                <h4 className="font-semibold text-sm mb-1">Description</h4>
                <p className="text-sm text-muted-foreground">{event.description}</p>
              </div>
              {event.tags && event.tags.length > 0 && (
                <div className="mb-4">
                  <h4 className="font-semibold text-sm mb-1">Tags</h4>
                  <div className="flex flex-wrap gap-1">
                    {event.tags.map((tag, i) => (
                      <Badge key={i} variant="outline" className="text-xs">
                        {tag}
                      </Badge>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      )}
      <CardFooter className="p-4 pt-0 flex justify-between border-t">
        <div className="flex gap-2">
          <Button
            variant="ghost"
            size="sm"
            className={`px-2 ${isLiked ? 'text-red-500' : ''}`}
            onClick={() => onAction(event, 'like')}
            title={isLiked ? 'Unlike' : 'Like'}
          >
            <Heart className={`h-4 w-4 mr-1 ${isLiked ? 'fill-red-500' : ''}`} />
            <span>{likeCount}</span>
          </Button>
          <Button
            variant="ghost"
            size="sm"
            className={`px-2 ${isSaved ? 'text-blue-500' : ''}`}
            onClick={() => onAction(event, 'save')}
            title={isSaved ? 'Unsave' : 'Save'}
          >
            <BookmarkIcon className={`h-4 w-4 mr-1 ${isSaved ? 'fill-blue-500' : ''}`} />
            <span>{saveCount}</span>
          </Button>
        </div>
        <div className="relative">
          <Button variant="outline" size="sm" className="px-4" onClick={() => setShowRsvpOptions(!showRsvpOptions)}>
            RSVP
          </Button>
          {showRsvpOptions && (
            <div className="absolute right-0 bottom-full mb-1 bg-background border rounded shadow-lg z-10">
              <Button
                variant="ghost"
                size="sm"
                className="w-full justify-start"
                onClick={() => {
                  onAction(event, 'rsvp', 'GOING');
                  setShowRsvpOptions(false);
                }}
              >
                Going
              </Button>
              <Button
                variant="ghost"
                size="sm"
                className="w-full justify-start"
                onClick={() => {
                  onAction(event, 'rsvp', 'MAYBE');
                  setShowRsvpOptions(false);
                }}
              >
                Maybe
              </Button>
              <Button
                variant="ghost"
                size="sm"
                className="w-full justify-start"
                onClick={() => {
                  onAction(event, 'rsvp', 'NOT_GOING');
                  setShowRsvpOptions(false);
                }}
              >
                Not Going
              </Button>
            </div>
          )}
        </div>
      </CardFooter>
    </Card>
  );
}

export default EventCard;
